'use strict';

// MODULES //

import { isPrimitive as isProbability } from '@stdlib/assert-is-probability' ;
import { isPrimitive as isNumber } from '@stdlib/assert-is-number' ;
import isObject from '@stdlib/assert-is-plain-object' ;
import { isPrimitive as isString } from '@stdlib/assert-is-string' ;
import isnan from '@stdlib/assert-is-nan' ;
import hasOwnProp from '@stdlib/assert-has-own-property' ;
import format from '@stdlib/error-tools-fmtprodmsg' ;


// MAIN //

/**
* Validates function options.
*
* @private
* @param {Object} opts - destination for validated options
* @param {Options} options - function options
* @param {number} [options.alpha] - significance level
* @param {string} [options.alternative] - alternative hypothesis (`two-sided`, `less` or `greater`)
* @param {number} [options.difference] - difference in means under H0
* @returns {(null|Error)} null or an error
*/
function validate( opts, options ) {
	if ( !isObject( options ) ) {
		return new TypeError( format( '0fu2h', options ) );
	}
	if ( hasOwnProp( options, 'alpha' ) ) {
		opts.alpha = options.alpha;
		if ( !isNumber( opts.alpha ) || isnan( opts.alpha ) ) {
			return new TypeError( format( '0fu8h', 'alpha', opts.alpha ) );
		}
		if ( !isProbability( opts.alpha ) ) {
			return new RangeError( format( 'invalid option. `%s` option must be a number on the interval: [0, 1]. Option: `%f`.', 'alpha', opts.alpha ) );
		}
	}
	if ( hasOwnProp( options, 'alternative' ) ) {
		opts.alternative = options.alternative;
		if ( !isString( opts.alternative ) ) {
			return new TypeError( format( '0fu2i', 'alternative', opts.alternative ) );
		}
	}
	if ( hasOwnProp( options, 'difference' ) ) {
		opts.difference = options.difference;
		if ( !isNumber( opts.difference ) || isnan( opts.difference ) ) {
			return new TypeError( format( '0fu8h', 'difference', opts.difference ) );
		}
	}
	return null;
}


// EXPORTS //

export default validate;
